.user-container {
  padding: 80px 0px;
}
.user-info h1 {
  font-weight: 700;
  color: #333;
}
.user-info p {
  color: #999;
}
.avatar-and-action > img,
.avatar-and-action > a {
  display: flex;
  flex-direction: column;
}
.avatar-and-action > a {
  margin-top: 30px;
}
.avatar-and-action {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.btn.btn-dark.btn-auth {
  border: 1px solid #333;
  padding: 8px 40px;
  text-transform: uppercase;
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -ms-border-radius: 0px;
  -o-border-radius: 0px;
}
.btn.btn-light,
.form-control {
  border-radius: 0px;
}
