@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  color: #333;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.product-rating {
  display: flex;
  align-items: center;
  justify-content: space-between;

  color: #d8d9da;
}

.search-bar {
  display: flex;
  align-items: center;
  background-color: #f4631b;
  height: 100px;
}
#logo {
  height: 50px;
  padding: 5px 0px;
}
.search-bar > .container {
  display: flex;
  justify-content: space-between;
}
.search-bar .input-group {
  align-self: center;
  height: 48px;
  max-width: 500px;
  margin-left: 20px;
}
.input-group input,
.input-group button {
  height: 48px;
}
.input-group button {
  height: 48px;
  padding: 0px 30px;
  border: 1px solid #333;
}
#search {
  background-color: #333;
  color: #fff;
  border: 1px solid #333;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
}
.whishlist-and-shopping-bag {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.whishlist-and-shopping-bag a {
  color: #fff;
  align-self: center;
}
.whishlist-and-shopping-bag__icon {
  font-size: 2rem;
  margin-left: 20px;
}

.dropdown-list {
  z-index: 1;
  background-color: #fff;
  position: absolute;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 10px 20px;
  box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.2);
  margin-bottom: 0px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.dropdown-list li {
  display: flex;
  align-items: center;
  padding: 3px 0px;
  list-style-type: none;
  margin-left: 0px;
}
.dropdown-list li a svg {
  padding-left: 20px;
}
.dropdown-list li a {
  text-decoration: none;
  font-weight: 500;
  color: #333;
}
.dropdown-list-currency {
  width: 70px;
  color: #333;
}

.dropdown-list-currency span {
  width: 20px;
  display: inline-block;
  font-weight: 700;
}
.dropdown-list li a img {
  margin-right: 10px;
}
.btn.btn-light.btn-dropdown-list:active,
.btn.btn-light.btn-dropdown-list {
  background-color: #fff;
  border: 1px solid #fff;
}

.navbar__header {
  background-color: #fff;
}
.navbar__header > .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0px;
}
.btn.btn-light.btn-dropdown-list,
.navbar__header-nav {
  text-transform: uppercase;
  color: #999;
  font-size: 0.8rem;
}
.navbar__header-nav > a {
  text-transform: uppercase;
  padding: 0px 10px;
  color: #999;
  font-size: 0.8rem;
}

.header-overlay {
  height: 60vh;
  position: absolute;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.65);
}
.header-container {
  background-image: url("https://devf-website.s3.amazonaws.com/static/assets/img/fondos/home.jpg");
  height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header-container > h1 {
  font-size: 5rem;
  font-weight: 700;
  color: #fff;
  z-index: 1;
}
.header-container > p {
  color: #fff;
  font-size: 1.5rem;
  z-index: 1;
}
.header-counters {
  display: flex;
  flex-direction: row;
  margin-top: 2rem;
  color: #fff;
  z-index: 1;
}
.header-counters > div {
  text-align: center;
  margin-right: 25px;
  margin-bottom: 40px;
}
.header-counters > div:last-child {
  margin-right: 0px;
}
.header-counters > div > h3 {
  font-size: 1.8rem;
  font-weight: 900;
  letter-spacing: 5px;
}
#all-products {
  z-index: 1;
  padding: 10px 40px;
  font-weight: 600;
  color: #666666;
}

.newsletter-block {
  color: #fff;
  padding: 30px 0px;
  background-color: #f4631b;
}
.newsletter-block div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.newsletter-block div:nth-child(2) {
  flex-direction: column;
}
.newsletter-block h4 {
  font-size: 1.4rem;
  font-weight: 700;
}
.newsletter-block h5 {
  font-size: 1.1rem;
  font-weight: 700;
}
.newsletter-block p {
  font-size: 0.9rem;
}
footer {
  padding: 30px 0px;
}
.footer-bottom > p {
  margin-bottom: 0px;
}
.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer-content {
  padding: 50px 0px;
  color: #333;
}
.contact-info {
  margin-top: 40px;
}
.contact-info,
.info-follow > h6 {
  display: flex;
  justify-content: space-between;
}
.info-detail > span {
  color: #999999;
  text-transform: uppercase;
}
.info-detail > h6,
.categories-info > h6 {
  font-weight: 600;
}
.categories-info {
  margin-left: 30px;
}
.categories-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  list-style: none;
  margin: 0px;
  padding: 0px;
}
.categories-list > li > a {
  color: #999;
  text-decoration: none;
}

.user-container {
  padding: 80px 0px;
}
.user-info h1 {
  font-weight: 700;
  color: #333;
}
.user-info p {
  color: #999;
}
.avatar-and-action > img,
.avatar-and-action > a {
  display: flex;
  flex-direction: column;
}
.avatar-and-action > a {
  margin-top: 30px;
}
.avatar-and-action {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.btn.btn-dark.btn-auth {
  border: 1px solid #333;
  padding: 8px 40px;
  text-transform: uppercase;
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -ms-border-radius: 0px;
  -o-border-radius: 0px;
}
.btn.btn-light,
.form-control {
  border-radius: 0px;
}

.contact-detail > div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 15px;
}
.contact-detail > div > svg {
  font-size: 2.5rem;
  background-color: #f4631b;
  color: #ffffff;
  padding: 8px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  margin-right: 15px;
}
.contact-detail > div > p {
  margin-bottom: 0px;
  font-size: 1.1rem;
  color: #999;
}
#form-contact {
  padding: 80px 0px;
}
.form-contact h2 {
  color: #999;
  font-weight: 600;
}
.form-contact h2 span {
  font-weight: 300;
}
.form-contact label {
  color: #999;
}

.home__container {
  height: 60vh;
}
.categories__container {
  background-color: #f2f2f2;
  padding: 60px 0px;
}
.categories__container > div {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}
.category-box a {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  padding: 30px;
  transition: 0.2s;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
}
.category-box a .category-box__icon,
.category-box a h5 {
  color: #333;
  transition: 0.2s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.category-box:hover {
  box-shadow: 0px 1px 2px 1px rgba(0, 0, 0, 0.1);
}
.category-box:hover .category-box__icon,
.category-box:hover h5 {
  color: #f4631b;
}
.category-box__icon {
  font-size: 4rem;
  display: block;
  margin-bottom: 15px;
}
.category-box {
  background-color: #fff;
}
.products-by-category {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}
.product-categoty__box:nth-child(2n) {
  background-color: #f2f2f2;
}
.single-product {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 300px;
  border-radius: 5px;
  background-color: #fff;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.single-product > a {
  text-decoration: none;
}
.single-product > a > img {
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  max-height: 180px;
  margin: auto;
}
.single-product > a > h6,
.single-product > h4 {
  margin: 10px 0px;
  padding: 0px 10px;
}
.single-product > a > h6 {
  font-weight: 300;
  color: #666;
}
.single-product > h4 {
  font-size: 1.3rem;
}
.single-product > a > img[src$="product.jpg"] {
  filter: grayscale(1) brightness(1.35);
  -webkit-filter: grayscale(1) brightness(1.35);
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.single-product > a > img[src$="product.jpg"]:hover {
  filter: grayscale(0) brightness(1);
  -webkit-filter: grayscale(0) brightness(1);
}
.quick-view {
  position: absolute;
  top: auto;
  background-color: #f4631b;
}
.rating-block {
  padding: 0px 10px;
}
.category-name svg {
  margin-top: -5px !important;
}
.category-name span {
  padding: 0px 10px;
  color: #999;
}

#form-container {
  border-top: 4px solid #f4631b;
  box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.2);
  padding: 50px 20px;
  margin: 0px 0px 60px 0px;
}
.form-container__title h1 {
  margin-top: 60px;
  margin-bottom: 30px;
  font-weight: 300;
  font-size: 1.8rem;
}
.form-container__title,
#form-container h3 {
  font-weight: 300;
  color: #f4631b;
}
#form-container h3 {
  text-transform: uppercase;
  font-size: 1.3rem;
  margin-bottom: 20px;
}
.form-group input {
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -ms-border-radius: 0px;
  -o-border-radius: 0px;
}

.btn.btn-primary.btn-auth,
.btn.btn-primary.btn-auth:active {
  background-color: #f4631b;
  border: 1px solid #f4631b;
  padding: 8px 40px;
  text-transform: uppercase;
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -ms-border-radius: 0px;
  -o-border-radius: 0px;
}
.login-copy,
.why-register {
  color: #999;
}
.form-group > label > span {
  color: #f4631b;
  font-weight: 900;
}

.product-detail {
  padding: 80px 0px;
}
.product-detail h1 {
  font-size: 1.8rem;
}
.product-detail p {
  margin-top: 40px;
  color: #999;
}
.stock-and-price {
  display: flex;
  justify-content: space-between;
  align-items: start;
  color: #666;
}
.product-detail h4 {
  color: #f4631b;
  font-weight: 700;
}
.btn.btn-primary.btn-product,
.btn.btn-primary.btn-product:active {
  background-color: #f4631b;
  border: 1px solid #f4631b;
  padding: 8px 40px;
  text-transform: uppercase;
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -ms-border-radius: 0px;
  -o-border-radius: 0px;
}
.breadcrumb {
  background-color: #fff;
  margin-bottom: 0px;
}

.breadcrumb-item > a {
  color: #000;
  font-weight: 700;
  text-decoration: none;
}

